body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.navbar.navbar-default.myNavBar {
  background: linear-gradient(45deg, #aaa3aa, #282c34);
}

.navbar.navbar-default.myNavBar a {
  color: white;
}

.nav.navbar-nav>li:not(:first-child) {
padding-left: 20px;
}

.nav.navbar-nav.myNavBar-nav {
  flex-direction: row;
  display: inline-flex;
}

.container-fluid.main-container {
  background :lightgray;
  min-height: 100vh;
  padding-top: 20px;
}

.container-fluid.main-container .card {
  float: left;
  margin-left: 20px;
  flex-direction: row;
  height: 12rem;
  margin-top: 20px;
}

.btn.btn-bottom-aligned{
  position: absolute;
  bottom: 5px;
}
.text-small {
  font-size: 14px;
  font-weight: bold;
  color : white;
}

.navbar-brand.myDetails {
  display: block;
  line-height: 0.5;
}

.big-plus {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 60px;
  color: black;
  padding-top: 40px;
  margin-left: 40px;
  font-weight: bold;
}

@media only screen and (max-width: 600px) {
  body {
    background-color: lightblue;
  }
}

